    .header {
        margin-top: 30px;
        width: 90vw;
        flex-grow: 1;
        max-width: 1150px;
        position: fixed;
        z-index: 3;
        left: 50%;
        top: 0;
        transform: translateX(-50%);
        transition: 0.5 ease-in-out all;
    }

